a{
    text-decoration: none;
}

.card{
    width: 34rem;
    height: 22rem;
    border: 1px solid #000000;
    margin: 0 auto 3rem;
    background: url('../../assets/banner/ui.jpg');
    background-size: cover;
    background-position-x: center;
}
/* details project */
.card .card-body{
    display: inline-block;
}
.card .card-body .card-title{
    color: var(--card-title);
}
.card .card-body .up:first-letter{
    text-transform: uppercase;
}
.card .card-text{
    color: white;
    font-style: normal;
    font-family: var(--georgia);
    margin-right: 1rem;
}
.card .card-link{
    margin-right: 1rem;
    color: var(--card-title);
    border-bottom: 3.5px solid #38ef7d;
    padding-bottom: .4rem;
}
.card-body i{
    margin-left: .5rem;
}
.card .card-body .card-subtitle{
    position: absolute;
    bottom: 5%;
    color: var(--card-title);
    font-style: italic;
}
.card:hover{
  animation: effectPublication .3s forwards;
  cursor: pointer;
}

@keyframes effectPublication{
    100%{
        transform: scale(1.1);
        box-shadow: 1px 1px 30px black;
    }  
}
  